import { createClient } from "@/prismicio";
import AboutMeSlice from "@/slices/AboutMeSlice";
import PfpSlice from "@/slices/PfpSlice";
import SocialSlice from "@/slices/SocialSlice";
import type { Metadata } from "next";

export default async function Home() {
  const client = createClient();
  const page = await client.getSingle("home");

  return (
    <main className="max-w-5xl mx-auto p-4">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {/* Manual layout for exact control */}
        <div className="lg:col-span-2">
          {page.data.slices.find(slice => slice.slice_type === "pfp") &&
            <PfpSlice slice={page.data.slices.find(slice => slice.slice_type === "pfp")!} />
          }
        </div>
        <div className="lg:row-span-2">
          {page.data.slices.find(slice => slice.slice_type === "about_me") &&
            <AboutMeSlice slice={page.data.slices.find(slice => slice.slice_type === "about_me")!} />
          }
        </div>
        <div className="lg:col-span-2">
          {page.data.slices.find(slice => slice.slice_type === "social") &&
            <SocialSlice slice={page.data.slices.find(slice => slice.slice_type === "social")!} />
          }
        </div>
      </div>
    </main>
  );
}

export async function generateMetadata(): Promise<Metadata> {
  const client = createClient();
  const page = await client.getSingle("home");

  return {
    title: page.data.meta_title,
    description: page.data.meta_description,
  };
}
